<template>
  <div class="main">
    <div class="image-center">
      <img src="@/assets/楼宇.png" alt="楼宇图片" class="center-img" />
    </div>
    <div class="left1">
      <div class="left-top">
        <img src="@/assets/楼图标.png" alt="楼宇图标" class="card-icon" />
        <span class="icon-text">数据总览</span>
        <div class="info-list">
          <div class="data-item">
            <span class="label">总用水量:</span>
            <span class="value">{{ AllWater }}</span>
            <span class="unit">m³</span>
          </div>
          <div class="data-item">
            <span class="label">生活用水:</span>
            <span class="value">{{ LivingWater }}</span>
            <span class="unit">m³</span>
          </div>
          <div class="data-item">
            <span class="label">空调用水:</span>
            <span class="value">{{ AirWater }}</span>
            <span class="unit">m³</span>
          </div>
        </div>
      </div>
      <div class="left-bot">
        <img src="@/assets/楼图标.png" alt="楼宇图标" class="card-icon" />
        <span class="icon-text"> 楼宇信息</span>
        <el-tree 
          :data="buildingTreeData" 
          :props="defaultProps" 
          @node-click="handleNodeClick" 
          class="building-tree"
          node-key="id" 
          default-expand-all
        >
          <template #default="{ node, data }">
            <div class="custom-tree-node" :class="`node-${data.type}`">
              <i :class="getNodeIcon(data.type)" class="tree-node-icon"></i>
              <span class="tree-node-label">{{ node.label }}</span>
              <span v-if="data.meterId" class="tree-node-id">{{ data.meterId }}</span>
              <span v-if="data.status" :class="['tree-node-status', data.status]">{{ data.status }}</span>
            </div>
          </template>
        </el-tree>
      </div>
    </div>
    <!-- 右侧按钮组 -->
    <right-button-group v-model="selectedButton" />

    <!-- 修改弹窗部分 -->
    <div v-if="showPopup" class="popup-div">
      <div class="popup-content">
        <div class="popup-header">
          <span class="popup-title">水表信息</span>
          <div class="chart-controls">
            <div class="time-dimension">
              <span v-for="item in timeDimensions" :key="item.value"
                :class="['dimension-item', { active: currentTimeDimension === item.value }]"
                @click="changeTimeDimension(item.value)">
                {{ item.label }}
              </span>
            </div>

            <div class="time-selector">
              <input type="date" v-if="currentTimeDimension === 'day'" v-model="selectedDate" class="date-input"
                @change="updateChartData" />
              <input type="month" v-else-if="currentTimeDimension === 'month'" v-model="selectedMonth"
                class="date-input" @change="updateChartData" />
              <input type="number" v-else-if="currentTimeDimension === 'year'" v-model="selectedYear"
                class="date-input year-input" min="2000" max="2030" @change="updateChartData" />
            </div>
          </div>
        </div>

        <!-- 添加图表容器 -->
        <div ref="popupChart" class="chart-container"></div>
        <button @click="closePopup">关闭</button>
      </div>
    </div>
  </div>
</template>

<script>
import RightButtonGroup from '@/components/RightButtonGroup.vue';
import * as echarts from 'echarts'; // 引入 ECharts

export default {
  components: {
    RightButtonGroup
  },
  data() {
    const now = new Date();
    return {
      showPopup: false,
      showChart: false,
      selectedMeterStatus: '',
      selectedMeterId: '',
      selectedMeterLabel: '',
      selectedButton: '用水计量',
      AllWater: 123456,
      LivingWater: 85421,
      AirWater: 38035,
      chartInstance: null,

      // 添加时间维度相关数据
      timeDimensions: [
        { label: '日', value: 'day' },
        { label: '月', value: 'month' },
        { label: '年', value: 'year' }
      ],
      currentTimeDimension: 'day', // 默认选择日
      selectedDate: this.formatDate(now), // 默认选择今天
      selectedMonth: `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, '0')}`, // 默认选择当前月
      selectedYear: now.getFullYear(), // 默认选择当前年

      // 模拟后端返回的扁平化数据
      backendData: [
        // 楼宇数据
        { id: 1, name: '楼宇一', type: 'building', parentId: null },
        
        // 房间数据
        { id: 2, name: '水泵房', type: 'room', parentId: 1 },
        { id: 6, name: '空调机房', type: 'room', parentId: 1 },
        { id: 8, name: '生活水箱', type: 'room', parentId: 1 },
        
        // 水表数据
        { id: 3, name: '水表1', type: 'meter', parentId: 2, meterId: 'W001', status: '正常' },
        { id: 4, name: '水表2', type: 'meter', parentId: 2, meterId: 'W002', status: '正常' },
        { id: 5, name: '水表3', type: 'meter', parentId: 2, meterId: 'W003', status: '故障' },
        { id: 7, name: '水表4', type: 'meter', parentId: 6, meterId: 'W004', status: '正常' },
        { id: 9, name: '水表5', type: 'meter', parentId: 8, meterId: 'W005', status: '正常' },
        { id: 10, name: '水表6', type: 'meter', parentId: 8, meterId: 'W006', status: '正常' }
      ],
      
      buildingTreeData: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    };
  },
  mounted() {
    // 模拟从后端获取数据并构建树形结构
    this.buildTreeFromBackendData();
  },
  methods: {
    // 添加日期格式化方法
    formatDate(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },

    // 切换时间维度
    changeTimeDimension(dimension) {
      this.currentTimeDimension = dimension;
      this.updateChartData();
    },

    // 更新图表数据
    updateChartData() {
      if (!this.chartInstance) return;

      let xAxisData = [];
      let seriesData = [];

      switch (this.currentTimeDimension) {
        case 'day':
          // 日视图：显示24小时数据
          xAxisData = Array.from({ length: 24 }, (_, i) => `${i}时`);
          seriesData = Array.from({ length: 24 }, () => Math.floor(Math.random() * 100));
          break;
        case 'month':
          // 月视图：显示当月每日数据
          const [year, month] = this.selectedMonth.split('-').map(Number);
          const daysInMonth = new Date(year, month, 0).getDate();
          xAxisData = Array.from({ length: daysInMonth }, (_, i) => `${i + 1}日`);
          seriesData = Array.from({ length: daysInMonth }, () => Math.floor(Math.random() * 500));
          break;
        case 'year':
          // 年视图：显示12个月数据
          xAxisData = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'];
          seriesData = Array.from({ length: 12 }, () => Math.floor(Math.random() * 2000));
          break;
      }

      // 更新图表配置
      const option = {
        xAxis: {
          data: xAxisData
        },
        series: [{
          data: seriesData
        }]
      };

      this.chartInstance.setOption(option);
    },

    handleNodeClick(data) {
      console.log('点击节点:', data);
      console.log('选中水表:', data.label, '水表ID:', data.meterId);
      this.showChart = true;
      this.selectedMeterId = data.meterId;
      this.selectedMeterLabel = data.label;
      this.selectedMeterStatus = data.status;
      this.showPopup = true;
      this.$nextTick(() => {
        this.initPopupChart(); // 初始化弹窗图表
      });
    },

    initPopupChart() {
      // 销毁之前的实例（如果存在）
      if (this.chartInstance) {
        this.chartInstance.dispose();
      }

      // 初始化新的图表实例
      this.chartInstance = echarts.init(this.$refs.popupChart);

      // 配置图表选项
      const option = {
        title: {
          text: `${this.selectedMeterLabel} 用水数据`,
          textStyle: {
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: [], // 空数据，后续更新
          axisLabel: {
            color: '#fff'
          }
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            color: '#fff'
          }
        },
        series: [{
          data: [],
          type: 'bar',
          itemStyle: {
            color: '#409EFF'
          }
        }]
      };

      // 设置图表选项
      this.chartInstance.setOption(option);

      // 更新图表数据
      this.updateChartData();
    },

    // 关闭弹窗方法
    closePopup() {
      this.showPopup = false;
      // 销毁图表实例以释放资源
      if (this.chartInstance) {
        this.chartInstance.dispose();
        this.chartInstance = null;
      }
    },

    // 根据后端数据构建树形结构
    buildTreeFromBackendData() {
      // 将扁平数据转换为映射表，便于查找
      const nodeMap = {};
      this.backendData.forEach(item => {
        nodeMap[item.id] = {
          ...item,
          label: item.name,
          children: []
        };
      });

      // 构建树形结构
      const treeData = [];
      this.backendData.forEach(item => {
        const node = nodeMap[item.id];
        
        if (item.parentId === null) {
          // 顶级节点（楼宇）
          treeData.push(node);
        } else {
          // 子节点，添加到父节点的children中
          if (nodeMap[item.parentId]) {
            nodeMap[item.parentId].children.push(node);
          }
        }
      });

      this.buildingTreeData = treeData;
    },

    // 获取节点图标
    getNodeIcon(type) {
      const iconMap = {
        building: 'el-icon-office-building',
        room: 'el-icon-school',
        meter: 'el-icon-monitor'
      };
      return iconMap[type] || 'el-icon-folder';
    }
  }
};
</script>

<style scoped>
/* 覆盖Element UI树节点的默认选中样式 */
:deep(.el-tree-node.is-current > .el-tree-node__content) {
  background-color: transparent !important;
}

:deep(.el-tree-node__content:hover) {
  background-color: rgba(64, 158, 255, 0.2) !important;
}

.label {
  color: #bbdefb;
  font-size: 14px;
}

.value {
    color: #4fc3f7;
    font-size: 18px;
    font-weight: 500;
    text-shadow: 0 0 5px rgba(79, 195, 247, 0.5);
}

.unit {
  color: #81d4fa;
  font-size: 14px;
}

.data-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.building-tree {
  margin-top: 40px;
  margin-left: 20px;
  color: #fff;
  background: transparent;
  overflow-y: auto;
  max-height: 500px;
}

::-webkit-scrollbar {
  width: 4px;
}

::-webkit-scrollbar-thumb {
  background: rgba(50, 64, 107, 0.6);
