}

.custom-tree-node:hover {
  background-color: rgba(64, 158, 255, 0.2);
}

.node-building {
  color: #FFD54F;
  font-weight: 500;
}

.node-room {
  color: #81C784;
  /* margin-left: 20px; */
}

.node-meter {
  color: #4FC3F7;
  /* margin-left: 40px; */
}

.tree-node-icon {
  margin-right: 8px;
  font-size: 16px;
  width: 20px;
  text-align: center;
}

.node-building .tree-node-icon {
  color: #FFD54F;
}

.node-room .tree-node-icon {
  color: #81C784;
}

.node-meter .tree-node-icon {
  color: #4FC3F7;
}

.tree-node-label {
  margin-right: 10px;
}

.tree-node-id {
  font-size: 12px;
