}

.tree-node-status.故障 {
  background-color: rgba(244, 67, 54, 0.2);
  color: #F44336;
}

.el-tree-node__content {
  color: #fff !important;
  height: auto !important;
  padding: 0 !important;
}

/* 修改弹窗样式，使其不影响其他操作 */
.popup-div {
  position: fixed;
  top: 200px;
  right: 400px;
  z-index: 999;
  pointer-events: none;
}

.popup-content {
  background-color: rgba(32, 65, 109, 0.7);
  padding: 20px;
  border-radius: 5px;
  color: white;
  width: 950px;
  height: 550px;
  text-align: center;
  pointer-events: auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
}

.popup-content h3 {
  margin-top: 0;
}

.chart-container {
  flex: 1;
  min-height: 0;
}

.popup-content button {
  margin-top: 15px;
  padding: 5px 15px;
  background-color: white;
  color: #409EFF;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  align-self: center;
}

/* 添加新的样式 */
.popup-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.popup-title {
  font-size: 20px;
  font-weight: 600;
  background: linear-gradient(to right, #00eeff, #00aaff);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 10px rgba(0, 200, 255, 0.3);
}

.chart-controls {
  display: flex;
  gap: 20px;
  align-items: center;
}

.time-dimension {
  display: flex;
  gap: 10px;
}

.dimension-item {
  padding: 6px 16px;
  background: rgba(30, 60, 120, 0.6);
  border-radius: 20px;
  cursor: pointer;
  color: #a0d0ff;
  transition: all 0.3s ease;
  font-size: 14px;
  border: 1px solid transparent;
}

.dimension-item:hover {
  background: rgba(50, 120, 220, 0.6);
  color: #fff;
  border-color: rgba(100, 180, 255, 0.5);
  box-shadow: 0 0 10px rgba(100, 180, 255, 0.3);
}

.dimension-item.active {
  background: linear-gradient(135deg, #00aaff, #0077ff);
  color: #fff;
  border-color: rgba(0, 200, 255, 0.7);
  box-shadow: 0 0 15px rgba(0, 150, 255, 0.5);
}

.time-selector {
  display: flex;
  align-items: center;
  gap: 10px;
}

.date-input {
  padding: 6px 12px;
  border-radius: 6px;
  border: 1px solid rgba(100, 180, 255, 0.4);
  background: rgba(20, 40, 80, 0.6);
  color: #fff;
  width: 130px;
  font-family: 'Courier New', monospace;
}

.date-input:focus {
  outline: none;
  border-color: #00aaff;
  box-shadow: 0 0 10px rgba(0, 150, 255, 0.5);
}

.date-input::-webkit-calendar-picker-indicator {
  filter: invert(1);
}

.separator {
  color: #fff;
}

.year-input {
  width: 100px;
}

.main {
  position: relative;
  width: 100%;
  height: 100vh;
}

.info-list {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-top: 60px;
  row-gap: 30px;
  align-items: flex-start;
  color: #fff;
}

.icon-text {
    position: absolute;
  left: 42px;
  top: 12px;
  color: #4fc3f7;
  font-size: 16px;
  text-shadow: 0 0 8px rgba(79, 195, 247, 0.7);
  letter-spacing: 1px;
}

.card-icon {
  position: absolute;
  left: 15px;
  top: 10px;
}

.image-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.left1 {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 20%;
  z-index: 2;
}

.left-top {
  position: absolute;
  left: 10px;
  top: 20px;
  width: 85%;
  height: 200px;
  background-color: rgba(13, 33, 63, 0.7);
  border: 1px solid rgba(64, 140, 229, 0.6);
  border-radius: 12px;
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 20px rgba(64, 140, 229, 0.3),
    inset 0 0 15px rgba(64, 140, 229, 0.2);
  backdrop-filter: blur(5px);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.left-top::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(100, 181, 246, 0.2), transparent);
  transition: 0.5s;
}

.left-top:hover::before {
  left: 100%;
}

.left-top:hover {
  transform: translateY(-5px);
  box-shadow:
    0 12px 40px rgba(0, 0, 0, 0.4),
    0 0 30px rgba(64, 140, 229, 0.5),
    inset 0 0 20px rgba(64, 140, 229, 0.3);
}

.left-bot {
  position: absolute;
  left: 10px;
  top: 5%;
  width: 85%;
  height: 600px;
  background: rgba(13, 33, 63, 0.7);
  border: 1px solid rgba(64, 140, 229, 0.6);
  border-radius: 12px;
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 20px rgba(64, 140, 229, 0.3),
    inset 0 0 15px rgba(64, 140, 229, 0.2);
  backdrop-filter: blur(5px);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.left-bot::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(100, 181, 246, 0.2), transparent);
  transition: 0.5s;
}

.left-bot:hover::before {
  left: 100%;
}

.left-bot:hover {
  transform: translateY(-5px);
