    0 12px 40px rgba(0, 0, 0, 0.4),
    0 0 30px rgba(64, 140, 229, 0.5),
    inset 0 0 20px rgba(64, 140, 229, 0.3);
}

.el-tree {
  background: transparent !important;
}

.el-tree-node__label {
  font-size: 14px;
}

.el-tree-node__expand-icon {
